import Axios from 'axios'

const server = process.env.REACT_APP_SERVER

export const register = async (params)=>{
    let request = Axios({
        method : "POST",
        data : params, 
        url : server +'/wp-json/rest-user/register'
    })

    return Promise.resolve(request)
}

export const tampols = async (params)=>{
    let request = Axios({
        method : "POST",
        data : params, 
        url : server +'/wp-json/rest-user/tampols'
    })

    return Promise.resolve(request)
}