import  React  from 'react'
import {Modal} from 'react-bootstrap'
// import FacebookLogin from 'react-facebook-login'
import GoogleLogin from 'react-google-login';
import * as Wordpress from '../services/Wordpress.js'
import {connect} from 'react-redux'
import {inputUser} from '../actioncreator'



class ModalRegister extends React.Component{
  constructor(props){
    super(props)
    this.state ={
      show : true,
      logedIn: false,
      sla : ''
    }
  }

 

  componentDidMount(){
    this.checkingEmail()
  }

  checkingEmail = ()=> {
    let email = localStorage.getItem("email")
    if(email===null){
      this.setState({
        show : true,
        logedIn: false,
        sla : process.env.REACT_APP_SLA
      })
    }
    else{
      this.setState({
        show : false,
        logedIn: true,
        sla : process.env.REACT_APP_SLA
      })
      this.createSession(email)
    }
  }
  
  createSession= async (email)=>{
    let register = await Wordpress.register({
      email : email,
    })

    if(register.data.code === 1000){
      this.setState({
          logedIn : true, 
          show: false
      })
    }
    await localStorage.setItem("email",email)
    this.props.inputUser(register.data.data)
  }
  handleClose = () => {
    this.setState({
      show : false,
    })
  };
  
  responseGoogle = async (googleUser)=>{
    let profile = googleUser.getBasicProfile()
    let register = await Wordpress.register({
      name  : profile.getName(),
      profile_picture : profile.getImageUrl(),
      email : profile.getEmail(),
      token_id :profile.getId()
    })

    if(register.data.code === 1000){
      this.setState({
          logedIn : true, 
          show: false
      })
    }
    await localStorage.setItem("email",profile.getEmail())
    this.props.inputUser(register.data.data)
  }

  render(){
    return (
      <>
        <Modal id="register-modal" show={this.state.show} onHide={this.handleClose}>
        
          <Modal.Body>
              <div>
                <h5> Login "Tampol Online" </h5>

                <div className="form-group group-register">
            
                <GoogleLogin
                    className="btn-block btn-primary"
                    clientId="1065136493014-ql91q7uefodnpjcholluphl1353pq4dp.apps.googleusercontent.com"
                    buttonText="Login"
                    onSuccess={this.responseGoogle}
                    onFailure={this.responseGoogle}
                    cookiePolicy={'single_host_origin'}
                />
            
               

            
                  <p className="group-register text-center">
                    Degan login saya menyetujui 
                    <a href={this.state.sla}> Term And Sevices </a>
                  </p>
                </div>
              </div>
          </Modal.Body>
        </Modal>
      </>
    );
  }

}
const mapStateToProps = (state)=>{
  return state
}
export default connect(mapStateToProps,{inputUser})(ModalRegister)