import React from 'react';
import './App.css';
import * as Bounce from 'bounce.js'
import ModalRegister from './component/ModalRegister';
import {connect} from 'react-redux'
import * as Wordpress from './services/Wordpress.js'

class App extends React.Component{

  constructor(props){
    super(props)
    this.state = {
      play : false,
      audioUrl :'', 
      slapImg : '',
      slapCount : 0,
      enemies_name :'',
      enemies_img :'',
      songVideo:'',
      quoteStyle:'',
      handImageDisplay : '',
      userName : false
    }
  }


  static getDerivedStateFromProps(props,state){
     
      if(props.user.data !== undefined){
        state.userName  = 'Tampoled by: '+props.user.data.user_email 
        return state
      }
      return true
  }



  componentDidMount(){
    this.setState({
      audioUrl : process.env.REACT_APP_SRC +"/slap.wav",
      slapImg : process.env.REACT_APP_SRC +"/slap.png",
      enemies_img :'https://www.w3schools.com/bootstrap4/img_avatar3.png',
      default_img : 'https://www.w3schools.com/bootstrap4/img_avatar3.png',
      quoteStyle : 'none',
      handImageDisplay : 'none',
      enemies_name :'Click Image For "Tampol"',
      userName : ''
    })
  
  }
  
  enterName = (e)=>{
    this.setState({
      enemies_name : e.target.value
    })
  }
  enterLink = (e) =>{
    this.setState({
      enemies_img : e.target.value
    })
  }
  slap =()=> {
      this.setState({
        play : true
      })
      let bounce = new Bounce();
      let audio = new Audio(this.state.audioUrl)
      audio.play()
      bounce.translate({
        from: { x: -300, y: 50 },
        to: { x: 0, y: 0 },
        duration : 50,
      })
      .scale({
        from  : {x:1 ,y:5},
        to  : {x:1,y:1},
      })
      .applyTo(document.querySelectorAll(".animation-target"));
      let lastSlap = this.state.slapCount ;

      this.setState({
          slapCount : lastSlap+1
      })
      if(this.state.slapCount > -1){
          this.setState({
            handImageDisplay : 'block'
          })
      }
      if(this.state.slapCount > 30){
        this.setState({
          songVideo:  "https://www.youtube.com/embed/91dhAgsc01M?autoplay=1",
          quoteStyle : 'block'
        })
      }

      if(this.state.slapCount === 1 && 
          (this.state.default_img!== this.state.enemies_name)
        )
      {
          Wordpress.tampols({
              email : this.props.user.data.user_email,
              enemies_name : this.state.enemies_name
          })
      }
  }

  changeImage = (e)=>{
    e.preventDefault();

    let reader = new FileReader();
    if(e.target.files[0]=== null ){
      return false
    }
    let file = e.target.files[0];

    reader.onloadend = () => {
      this.setState({
        file: file,
        enemies_img: reader.result,
        slapCount : 0
      });
    }

    reader.readAsDataURL(file)
    this.slap()
  }

  render(){
    return(
      <div className="container-fluid">
         <ModalRegister display={this.state.displayRegister}/>
         <div className="row">
            <div className="col-md-3">
                <h4 className="text-center slap-counter">
                    "Tampol" Counter : 
                </h4>
                <h1 className="text-center">{this.state.slapCount}</h1>
              
            </div>
            <div className="col-md-4">
                <div className="animation-target" onClick={this.slap}>    
                    <img 
                    style={{display:this.state.handImageDisplay}}
                    alt="HandSlap"
                    src={this.state.slapImg}>
                    </img>
                </div>
               
                <div className="card" >
                  <div className="card-body enemy-image">
                    <div className="card-img-overlay" onClick={this.slap}>
                      <h5 className="card-title">
                        {this.state.enemies_name}
                      </h5>
                      <div className="watermark">

                        <p> <span>{this.state.userName}</span></p>
                      </div>
                    </div>
                     <img 
                      className="card-img enemy-foto" 
                      src={this.state.enemies_img}
                      alt="EnemyFoto"
                      />
                  </div>
                </div>
                <div className="card">
                  <div className="card-body">
                    <label className="label-file-upload"  htmlFor="upload-file"> Upload File</label>
                    <input 
                    style     = {{display: 'none'}} 
                    type      = 'file' id="upload-file" 
                    className = "upload-file"
                    onChange   = {(e)=>this.changeImage(e)}
                    >
                    </input>
                  </div>
                  {/* Input Enemies Name */}
                  <input onKeyUp={(e)=>this.enterName(e)} 
                  placeholder="Insert your enemies name" 
                  className="form-control input-lg"></input>
                </div>
                <div className="card-footer">
                  
                  <button 
                    className="btn btn-block btn-danger btn-lg btn-tampol" 
                    onClick={this.slap}>
                      Tampols It Baby!!!
                  </button>
                </div>
            </div>
            <div className="col-md-4 slap-counter">
              <iframe 
              title = "song"
              width="100%" height="300px" src={this.state.songVideo}
              frameBorder="0"
              allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" 
              >
              </iframe>
              <p className="text-muted text-center" style={{display : this.state.quoteStyle}}>
              I offer you peace, I offer you love, I offer you friendship. I see your beauty, I hear your need, I feel your feelings. My wisdom flows from the highest source. I salute that Source in you. Let us work together for unity and peace 
              <span>- Mahatma Gandhi</span>
              </p>
  
              </div>
          </div>
          <div className="row text-center">
              <div className="col-md-12 navbar text-white fixed-bottom navbar-expand-sm navbar-dark bg-dark">
              Tampolin - 2019 by  :  
              <a href="https://www.youtube.com/agitnaeta"> @agitnaeta
              </a>
              </div>
          </div>
      </div>
    
    )
  }

}

const stateToProps = state =>{
  return state
}
export default connect(stateToProps)(App);
